  }
  .icon-sousuo:active{
    color: #1056a7;
  }
  .search-input{
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    top: 0;
    left: 0;
    padding-left: 5px;
    padding-right: 32px;
    outline: none;
    border: 1px solid #c6d1de;
  }
}
</style>



